import { UserService } from './../../services/user.service';
import { FormGroup, FormControl } from '@angular/forms';
import { IllinessService } from './../../services/illiness.service';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-saude',
  templateUrl: './saude.page.html',
  styleUrls: ['./saude.page.scss'],
})
export class SaudePage implements OnInit {
  form: FormGroup
  illiness;
  arrIliness = [];
  constructor(
    private router: Router,
    private illinessSrv: IllinessService,
    private userSrv: UserService
  ) { }

  ngOnInit() {
    this.loadIlliness()
    this.initialForm()
  }

  initialForm(){
    this.form = new FormGroup({
      illiness: new FormControl(null)
    })
  }
  nextPage(){
    this.form.value.illiness = this.arrIliness
    this.userSrv.insertIllinessUser(this.form.value).subscribe(()=>{
      
      this.router.navigate(['tabs']);
    })
  }

  loadIlliness(){
    this.illinessSrv.getIlliness().subscribe(data=>{
      this.illiness = data;
    })
  }
  onBack(){
    this.router.navigate(['exercicio'])
  }

  toogle(event){
    if(event.detail.checked === true){
      this.arrIliness.push(event.detail.value) 
      console.log(this.arrIliness)
   }else{
    let arr =  this.arrIliness.filter(arrIliness => arrIliness !== event.detail.value )
    this.arrIliness = arr 
    console.log(this.arrIliness);
   }
  }

}
